.login {
     /* background: url(../../images/bg.jpg); */
     /* background-size: cover; */
     /* background-repeat: repeat; */
     display: flex; /* Используем flex-контейнер для центрирования по вертикали и горизонтали */
     justify-content: center;
     align-items: center;
     height: 100vh; /* Высота экрана */
     background-repeat: no-repeat;
    }

/* весь блок */

.form {
    position: absolute; /* Абсолютное позиционирование для центрирования по экрану */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); /* Центрирование по вертикали и горизонтали */
    font-family: 'Noto Sans', sans-serif; /* Выбор шрифта для текста в форме */
    background-color: #313338; /* Цвет фона формы */
    padding: 20px; /* Отступ внутри формы */
    border-radius: 5px; /* Скругление углов формы */
    width: 350px; /* Ширина формы */
    border: 2px solid #9FC4F0; /* Обводка формы */
    box-shadow: 5px 7px 2px rgba(0, 0, 0, 0.247);
}

/* надпись вход */

.form h1 {
    margin: 40px;
    padding: 0;
    text-align: center; /* Центрирование текста заголовка */
    color: #FFF9FC; /* Цвет текста заголовка */
}

/* блоки ввода */

.input_form {
    margin: 15px 0; /* Внешний отступ сверху и снизу блока ввода */
    
}

/* надписи логин и пароль */

.input_form label {
    
    color: #ffffffcd; /* Цвет текста метки */
    margin-bottom: 5px; /* Отступ снизу метки */
    display: block; /* Размещаем метку на новой строке */
    margin-left: 5%;
}

/* поля ввода */

.input_form .username, .input_form .input_form_password {
    font-family: 'Noto Sans', sans-serif;
    padding: 10px; /* Внутренний отступ вокруг текста в поле ввода */
    width: 90%; /* Занимает 90% доступной ширины */
    outline: none; /* Убираем контур фокуса на полях ввода */
    box-sizing: border-box; /* Учитываем границу в расчетах ширины */
    background-color: #1D2125; /* Цвет фона полей ввода */
    color: #FFF9FC; /* Цвет текста в полях ввода */
    border-radius: 5px; /* Скругление углов полей ввода */
    border: 1px solid #1D2125; /* Границы */
    margin-left: 5%;
}

.input_form_password input, .focus_input_form_password input{
    background-color: #1D2125;
    outline: none;
    box-sizing: border-box;
    color: #FFF9FC;
    width: 90%;
    border: none;
}

.focus_input_form_password {
    font-family: 'Noto Sans', sans-serif;
    padding: 10px; /* Внутренний отступ вокруг текста в поле ввода */
    width: 90%; /* Занимает 90% доступной ширины */
    outline: none; /* Убираем контур фокуса на полях ввода */
    box-sizing: border-box; /* Учитываем границу в расчетах ширины */
    background-color: #1D2125; /* Цвет фона полей ввода */
    color: #FFF9FC; /* Цвет текста в полях ввода */
    border-radius: 5px; /* Скругление углов полей ввода */
    border: 1px solid #4361EE; /* Границы */
    margin-left: 5%;
}

.icon{
    margin-left: 2%;
}

/* Определение стилей для фокусировки на полях ввода */

.input_form .username:focus {
    border: 1px solid #4361EE; /* Изменение цвета границы при фокусировке */
}


/* кнопка */

.input_form input[type="submit"] {
    margin-top: 50px; /* Внешний отступ сверху кнопки */
    background-color: #4361EE; /* Цвет фона кнопки */
    color: white; /* Цвет текста на кнопке */
    border: none; /* Убираем границу кнопки */
    padding: 10px; /* Внутренний отступ вокруг текста на кнопке */
    cursor: pointer; /* Указатель при наведении на кнопку */
    border-radius: 4px; /* Скругление углов кнопки */
    width: 50%;
    margin-left: 25%;
    margin-right: 25%;
    font-size: 15px;
    box-shadow: 5px 7px 2px rgba(0, 0, 0, 0.247);
}

/* Определение стилей для наведения на кнопку */

.input_form input[type="submit"]:hover {
    background-color: #3349C0; /* Изменение цвета фона при наведении на кнопку */
}

.reg_button{
    text-decoration: none;
    color:#4361EE;
}

.reg_button:hover{
    color:#3349C0;
}

.text{
    text-align: center;
    margin-top: 20px;
    color: #FFFFFF;
    font-weight: 300;
    margin-bottom: 0;
    font-size: 15px;
}


.offscreen {
    display: none;
}

.errmsg {
    margin-top: 5px;
    font-size: 14px;
    color: red;
    margin-left: 5%;
}